<template>
  <div id="toast" v-show="isShow">
    {{message}}
  </div>
</template>

<script>
  export default {
    name: "Toast",
    /*    props:{
          message:{
            type:String,
            default:'1111'
          },
          isShow:{
            type: Boolean,
            default: false
          }
        }*/
    data() {
      return {
        message: '',
        isShow: false
      }
    },
    methods: {
      changeShow(message, timer) {
        this.message = message
        this.isShow = true
        setTimeout(() => {
          this.message = ''
          this.isShow = false
        }, timer)
      }
    }
  }
</script>

<style scoped>
  #toast {
    width: 60%;
    height: 100px;
    background-color: rgba(0, 0, 0, .7);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    z-index: 999;
    color: #eaffea;
    border-radius: 10px;
    text-align: center;
    line-height: 100px;
    font-size: 20px;
  }
</style>
