import Vue from "vue";
import Vuex from "vuex"
import mutations from "@/store/mutations";
import actions from "@/store/actions";
import getters from "@/store/getters";

Vue.use(Vuex)

const state = {
  //设置home活跃tapcontrol-item的默认值
  //  统一两个 tapControl中的adciveitem的值一致
  activeindex: 0,
  //保存每次离开home时的滚动偏移量
  savehomeY: 0,
//  保存购物车内部的商品
  shopCart: []
}

const store = new Vuex.Store({
  state,
  mutations,
  getters,
  actions,
  modules: {}
})
