export default {
//  将商品添加进入购物车
  addProduct(context, payload) {
    return new Promise((resolve, reject) => {
      //  判断购物车内部是否已拥有此商品
      let oldProduct = context.state.shopCart.find(item => item.iid === payload.iid)

      if (oldProduct) {
        resolve('该商品数量+1')
        context.commit('addCounter', oldProduct)
      } else {
        resolve('成功加入该新商品')
        context.commit('addOneGood', payload)
      }
    })
  }
}
