export default {
  //修改home活跃tapcontrol-item的值
  changeActiveItem(state, index) {
    state.activeindex = index
  },
  //修改每次离开home时的滚动偏移量
  changeHomeY(state, y) {
    state.savehomeY = y
  },
  addCounter(state, oldProduct) {
    oldProduct.count += 1
  },
  addOneGood(state, payload) {
    state.shopCart.push(payload)
  },
  ChangeItemChecked(state, payload) {
    state.shopCart[payload.index].checked = !payload.isActive
  },
  AllProductChecked(state){
    state.shopCart.forEach(item => item.checked = true)
  },
  AllProductDisChecked(state){
    state.shopCart.forEach(item => item.checked = false)
  }
}
