<template>
  <div class="TabBar-item" @click="changUrl">
<!--    组件替换时会替换整个插槽，包裹div标签为这个插槽插入的元素添加一些状态-->
     <div v-if=!isActive>
       <slot name="item-logo"></slot>
     </div>
    <div v-else>
      <slot name="item-logo-active"></slot>
    </div>
    <div :style=isColorStyle>
      <slot name="item-text"></slot>
    </div>

  </div>
</template>

<script>
  export default {
    name: "TabBar-item",
/*    data(){
      return{
        isActive:true
      }
    },*/
    props:{
      path:String,
      textColorStyle:{
        type:String,
        default:'red'
      }
    },
    computed:{
      //判断活跃路由是否就是这个组件传递的path路由，如果是，就说明这个组件是活跃的正被显示的组件
      isActive(){
        return  this.$route.path.indexOf(this.path) !== -1
      },
      //若该组件活跃就传递this.textColorStyle的值给这个组件的style
      isColorStyle(){
        return this.isActive? {color:this.textColorStyle}:{}
      }
    },
    methods:{
      changUrl(){
        console.log(this.path);
        this.$router.replace(this.path)
      }
    }
  }
</script>

<style scoped>
  .TabBar-item{
    flex: 1;
    text-align: center;
    height: 49px;
    font-size: small;
  }
  .TabBar-item img{
    height: 30px;
    width: 30px;
  }
</style>
